/*-----------mix-ins----------*/
.button {
  border-radius: 4px;
  padding: 2em 3.5em 2em 3.5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
          box-shadow: 0px 3px 5px 0px #b0b8bd;
  margin: 2em 1em 5em 1em;
  cursor: pointer;
  max-width: 30%;
}
.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
/*------------ flex -----------*/
.flex-page {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
