/*----------- @media ----------*/
@media only screen and (device-width: 768px), only screen and (max-width: 768px) {
  body {
    font-size: 15px;
  }
}
/*----------- ----------*/
body {
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  font-size: 15px;
  margin: auto;
}
a {
  text-decoration: none;
}
.App {
  height: 100vh;
  width: 100vw;
}
/*-------------------- NAVIGATION BUTTONS ---------*/
.NavButton {
  color: #ffffff;
  background-color: #02b196;
  border-radius: 4px;
  padding: 2em 3.5em 2em 3.5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
