  margin: 2em 1em 5em 1em;
  cursor: pointer;
  max-width: 30%;
}
.NavButton:hover {
  background-color: #057a64;
}
.NavButtonGreyed {
  color: #f1f1f1;
  background-color: #bdb6b0;
  border-radius: 4px;
  padding: 2em 3.5em 2em 3.5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
