/*-------------------- FORM STYLING ---------*/
input {
  border: none;
  height: 2em;
  font-size: inherit;
}
.FormLine {
  border-bottom: #98192a 2px solid;
  margin: 0px auto;
}
.PageContent {
  width: 100%;
  padding-top: 2em;
}
@-webkit-keyframes slide {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}
@keyframes slide {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}


